import React, { Component } from "react";
import items from './data';

const RoomContext = React.createContext();

class RoomProvider extends Component {
	state = {
		rooms: [],
		sortedRooms: [],
		featuredRooms: [],
		loading: true,
		type: 'all',
		capacity: 1,
		price: 0,
		minPrice: 0,
		maxPrice: 0,
		minSize: 0,
		maxSize: 0,
		breakfast: false,
		pets: false
	};

	// function that runs when all components mount
	componentDidMount() {
		let rooms = this.formatData(items);
		let featuredRooms = rooms.filter((room) => room.featured === true);

		// calculate max price and max size for rooms
		let maxPrice = Math.max(...rooms.map(item => item.price));
		let maxSize = Math.max(...rooms.map(item => item.size));


		this.setState({
			rooms,
			featuredRooms,
			sortedRooms: rooms,
			loading: false,
			price: maxPrice,
			maxPrice,
			maxSize
		});
	}

	handleChange = event => {
		const target = event.target;
		const type = event.target.type;
		const name = event.target.name;
		const value = target.type === 'checkbox' ? target.checked : target.value;

		this.setState({
			[name]: value
		}, this.filterRooms);
	}

	filterRooms = () => {
		let {
			rooms, type, capacity, price, minSize, maxSize, breakfast, pets
		} = this.state;

		// get all rooms
		let tempRooms = [...rooms];

		// transform to int
		capacity = parseInt(capacity);

		// filter by type
		if(type!='all') {
			tempRooms = tempRooms.filter(room => room.type === type);
		}

		// filter by capacity
		if(capacity != 1) {
			tempRooms = tempRooms.filter(room => room.capacity >= capacity);
		}

		// filter by price
		tempRooms = tempRooms.filter(room => room.price <= price);

		// filter by size
		tempRooms = tempRooms.filter(room=>room.size>=minSize && room.size<=maxSize);

		// filter by extras
		if(breakfast) {
			tempRooms = tempRooms.filter(room => room.breakfast === true);
		}
		if(pets) {
			tempRooms = tempRooms.filter(room => room.pets === true);
		}

		// change state
		this.setState({
			sortedRooms: tempRooms
		});
	}

	formatData(items) {
		let tempItems = items.map(item => {

			// Create variables for better structuring 
			let id = item.sys.id;

			let images = item.fields.images.map(image => image.fields.file.url);

			// Structure object
			let room = {...item.fields, images, id};

			return room;
		});

		return tempItems;
	}

	getRoom = (slug) => {
		let tempRooms = [...this.state.rooms];
		const room = tempRooms.find((room)=>room.slug === slug);
		return room;
	}

    	render() {
		return(
			// pass different variables (value and getRoom in Room context provider)
			<RoomContext.Provider value={{
				...this.state,
				getRoom:this.getRoom,
				handleChange: this.handleChange
			}}>

				{this.props.children}

			</RoomContext.Provider>
		); 
    	}
}

const RoomConsumer = RoomContext.Consumer;

// Higher order function (returns another function)
// This is done to avoid complex coding in RoomsContainer.js
export function withRoomConsumer(Component) {
	return function ConsumerWrapper(props) {
		return (
			<RoomConsumer>
				{value => <Component {...props} context={value}/>}
			</RoomConsumer>
		);
	}
}

export { RoomProvider, RoomConsumer, RoomContext };
